<template>
  <div class="">
    <Button 
      label=""
      class=""
      size="small"
      severity="warning"
      icon="pi pi-pencil"
      @click="openModal()"
      v-tooltip.left="`Update User`"
    />
    <Dialog v-model:visible="visible" :modal="true" class="p-fluid" header="Update User" :breakpoints="{ '960px': '80vw' }" :style="{ width: '50vw' }" @hide="hideModal()">
      <div class="" style="">
        <div class="flex flex-column gap-2 mt-2">
          <label for="nama">Nama</label>
          <InputText id="nama" v-model="dataForm.nama" aria-describedby="nama-help" :class="{'p-invalid': v$.dataForm.nama.$invalid}"/>
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="nip">Nip</label>
          <InputText id="nip" v-model="dataForm.nip" aria-describedby="nip-help" :class="{'p-invalid': v$.dataForm.nip.$invalid}"/>
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="username">Username</label>
          <InputText id="username" v-model="dataForm.username" aria-describedby="username-help" :class="{'p-invalid': v$.dataForm.username.$invalid}" disabled/>
        </div>
        <!-- <div class="flex flex-column gap-2 mt-2">
          <label for="password">Password</label>
          <div class="p-inputgroup flex-1">
            <InputText :type="showPassword ? 'text' : 'password'" id="password" v-model="dataForm.password" aria-describedby="password-help" :class="{'p-invalid': v$.dataForm.password.$invalid}"/>
            <Button :icon="showPassword ? 'pi pi-eye' : 'pi pi-eye-slash'" @click="showPassword = !showPassword"/>
          </div>
        </div> -->
        <div class="flex flex-column gap-2 mt-2">
          <label for="role">Role</label>
          <Dropdown 
            v-model="dataForm.role" 
            :options="$store.state.data_static.role" 
            optionLabel="text" 
            optionValue="value" 
            placeholder="piilh role" 
            class="w-full" 
            :class="{'p-invalid': v$.dataForm.role.$invalid}" 
          />
        </div>
      </div>
      <template #footer>
        <div class="flex justify-content-end">
          <Button 
            label="Save"
            class=""
            size="small"
            :disabled="!isValid && !isDirty"
            v-tooltip.left="`save`"
            @click="submit()"
          />
        </div>
      </template>
    </Dialog>
  </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  props: [ 'data' ],
  emits: [ 'refresh' ],
  components: {
  },
  setup () {
    return { v$: useVuelidate() }
  },
  data() {
    return {
      // showPassword: false,
      visible: false,
      dataForm: {
        username: null,
        password: null,
        role: null,
        nama: null,
        nip: null,
      },
    };
  },
  computed: {
    isValid() {
      return !this.v$.dataForm.$invalid;
    },
    isDirty() {
      return this.v$.dataForm.$anyDirty;
    },
  },
  validations () {
    return {
      dataForm: {
        username: { required },
        password: { required },
        role: { required },
        nama: { required },
        nip: { required },
      }
    }
  },
  mounted() {
  },
  methods: {
    openModal(){
      const vm = this
      vm.visible = true
      vm.dataForm = JSON.parse(JSON.stringify(vm.data))
      vm.v$.$reset();
    },
    hideModal(){
      const vm = this
      vm.visible = false
    },
    async submit(){
      const vm = this
      const res = await vm.$axios.post('users/update', vm.dataForm)
      if(res.data.status == 200){
        vm.visible = false
        vm.$emit('refresh')
        vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Update User', life: 3000 });
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Update User', life: 3000 });
      }
    },
  },
};
</script>