<template>
  <div class="">
    <Button 
      label="Add"
      class="mb-4"
      size="small"
      icon="pi pi-plus"
      @click="openModal()"
      v-tooltip.left="`Register User`"
    />
    <Dialog v-model:visible="visible" :modal="true" class="p-fluid" header="Register User" :breakpoints="{ '960px': '80vw' }" :style="{ width: '50vw' }" @hide="hideModal()">
      <div class="" style="">
        <div class="flex flex-column gap-2 mt-2">
          <label for="nama">Nama</label>
          <InputText id="nama" v-model="dataForm.nama" aria-describedby="nama-help" :class="{'p-invalid': v$.dataForm.nama.$invalid && afterSubmit}"/>
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="nip">Nip</label>
          <InputText id="nip" v-model="dataForm.nip" aria-describedby="nip-help" :class="{'p-invalid': v$.dataForm.nip.$invalid && afterSubmit}"/>
        </div>
        <div class="flex flex-column gap-2">
          <label for="username">Username</label>
          <InputText id="username" v-model="dataForm.username" aria-describedby="username-help" :class="{'p-invalid': v$.dataForm.username.$invalid && afterSubmit}"/>
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="password">Password</label>
          <div class="p-inputgroup flex-1">
            <InputText :type="showPassword ? 'text' : 'password'" id="password" v-model="dataForm.password" aria-describedby="password-help" :class="{'p-invalid': v$.dataForm.password.$invalid && afterSubmit}"/>
            <Button :icon="showPassword ? 'pi pi-eye' : 'pi pi-eye-slash'" @click="showPassword = !showPassword"/>
          </div>
          <!-- <Password id="password" v-model="dataForm.password" aria-describedby="password-help" :class="{'p-invalid': v$.dataForm.password.$invalid && afterSubmit}"/> -->
        </div>
        <div class="flex flex-column gap-2 mt-2">
          <label for="role">Role</label>
          <Dropdown 
            v-model="dataForm.role" 
            :options="$store.state.data_static.role" 
            optionLabel="text" 
            optionValue="value" 
            placeholder="piilh role" 
            class="w-full" 
            :class="{'p-invalid': v$.dataForm.role.$invalid && afterSubmit}" 
          />
        </div>
      </div>
      <template #footer>
        <div class="flex justify-content-end">
          <Button 
            label="Save"
            class=""
            size="small"
            v-tooltip.left="`save`"
            @click="submit()"
          />
        </div>
      </template>
    </Dialog>
  </div>
</template>

<script>
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  emits: [ 'refresh' ],
  components: {
  },
  setup () {
    return { v$: useVuelidate() }
  },
  data() {
    return {
      afterSubmit: false,
      showPassword: false,
      visible: false,
      dataForm: {
        username: null,
        password: null,
        role: null,
        nama: null,
        nip: null,
      },
    };
  },
  computed: {
    isValid() {
      return !this.v$.dataForm.$invalid;
    },
    isDirty() {
      return this.v$.dataForm.$anyDirty;
    },
  },
  validations () {
    return {
      dataForm: {
        username: { required },
        password: { required },
        role: { required },
        nama: { required },
        nip: { required },
      }
    }
  },
  mounted() {
  },
  methods: {
    openModal(){
      const vm = this
      vm.visible = true
      vm.v$.$reset();
    },
    hideModal(){
      const vm = this
      vm.visible = false
      vm.dataForm = {
        username: null,
        password: null,
        role: null,
        nama: null,
        nip: null,
      }
    },
    async submit(){
      const vm = this
      vm.afterSubmit = true
      if(vm.isValid){
        const res = await vm.$axios.post('users/register', vm.dataForm)
        if(res.data.status == 200){
          vm.afterSubmit = false
          vm.visible = false
          vm.$emit('refresh')
          vm.$toast.add({ severity: 'success', summary: 'Konfirmasi', detail: 'Berhasil Register User', life: 3000 });
        }else{
          vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Gagal Register User', life: 3000 });
        }
      }else{
        vm.$toast.add({ severity: 'error', summary: 'Konfirmasi', detail: 'Data Tidak Valid', life: 3000 });
      }
    },
  },
};
</script>